<template>
  <div class="addAddress">
      <Header :isLeft='true' :title='title'/>
      <!-- 添加地址 -->
      <div class="viewbody">
          <div class="content">
              <FormBlock label='联系人' v-model="addressInfo.name" placeholder='姓名' :sex='addressInfo.sex' :tags='sexes' @checkSex='checkSex'/>
              <FormBlock label='电话' v-model="addressInfo.phone" placeholder='手机号码'/>
              <FormBlock v-model="addressInfo.address" @click='showSearch=true' label='地址' placeholder='小区/写字楼/学校等' icon='angle-right'/>
              <FormBlock label='门牌号' v-model="addressInfo.bottom" placeholder='10号楼5单元404' icon='edit' textarea='textarea'/>
              <div class="formblock">
                <div class="label-wrap">标签</div>
                <TabTag :tags='tags' @checkTag='checkTag' :selectTag='addressInfo.tag'/>
              </div>
          </div>

          <!-- 确定按钮 -->
          <div class="form-button-wrap">
            <button @click='handleSave' class="form-button">确定</button>
          </div>
          
      </div>
      <!-- 搜索地址 -->
      <AddressSearch :addressInfo='addressInfo' @close='showSearch=false' :showSearch='showSearch'/>
  </div>
</template>

<script>
import Header from '../../components/Header'
import {ref,reactive,onMounted,watch} from '@vue/composition-api'
import FormBlock from '../../components/Orders/FormBlock'
import TabTag from '../../components/Orders/TabTag'
import AddressSearch from '../../components/Orders/AddressSearch'
import { Toast } from 'mint-ui';
import axios from 'axios'
export default {
    name: "AddAddress",
    components: {
        Header,
        FormBlock,
        TabTag,
        AddressSearch
    },
    beforeRouteEnter(to, from, next) {
      // console.log(to);
      next(vm=>{
        vm.addressInfo=to.params.addressInfo
        vm.title=to.params.title
        // console.log(to);
      })

    },
    setup(props,{root}){

        const title=ref('')
        const tags=reactive(
          ['家','学校','公司']
        )
        const sexes=reactive(
          ['先生','女士']
        )
        const showSearch=ref(false)
        const addressInfo=ref({})

        const checkTag=(item)=>{
          // console.log(item);
          addressInfo.value.tag=item
        }

        const checkSex=(item)=>{
          // console.log(item);
          addressInfo.value.sex=item
          // console.log(addressInfo.value.sex);
        }

        const handleSave=()=>{
          if(!addressInfo.value.name){
            showMag('请输入联系人')
            return
          }

          if(!addressInfo.value.phone){
            showMag('请输入手机号码')
            return
          }

          if(!addressInfo.value.address){
            showMag('请输入收货地址')
            return
          }
          

          //存储数据
          if(title.value=='添加地址'){
            addAddress()
          }else{
            editAddress()
          }
          
        }
        onMounted(() => {
          // console.log(addressInfo.value.sex);
        })

        const showMag=(msg)=>{
          Toast({
            message: msg,
            position: 'bottom',
            duration: 5000
          });
        }

        const addAddress=()=>{
          axios.post(`/api/user/add_address/${localStorage.ele_login}`,addressInfo.value).then(res=>{
            // console.log(res.data);
            if(!root.$store.getters.userInfo){
              root.$store.dispatch('setUserInfo',addressInfo.value)
            }
            root.$router.push('myAddress')
          }).catch(err=>{
            console.log(err);
          })
        }

        const editAddress=()=>{
          axios.post(`/api/user/edit_address/${localStorage.ele_login}/${addressInfo.value._id}`,addressInfo.value).then(res=>{
            // console.log(res.data);
            root.$router.push('myAddress')
          }).catch(err=>{
            console.log(err);
          })
        }

        return {
            title,
            tags,
            checkTag,
            addressInfo,
            checkSex,
            handleSave,
            sexes,
            showSearch
        }
    }
}
</script>

<style scoped>
.addAddress {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding-top: 45px;
}
.viewbody {
  width: 100%;
  height: 100%;
  overflow: auto;
  box-sizing: border-box;
  background-color: #f5f5f5;
}

.content {
  padding-left: 4vw;
  background: #fff;
  font-size: 0.94rem;
}

.formblock {
  background-color: #fff;
  border-bottom: 1px solid #eee;
  display: flex;
}
.formblock .label-wrap {
  flex-basis: 17.333333vw;
  padding: 3.733333vw 0;
  line-height: 4.8vw;
  color: #333;
  font-weight: 700;
}

/* 确定按钮 */
.form-button-wrap {
  padding: 5.333333vw 4vw;
  display: flex;
}
.form-button-wrap .form-button {
  background: #00d762;
  text-align: center;
  border-radius: 0.533333vw;
  flex: 1;
  font-size: 1.1rem;
  line-height: 5.066667vw;
  color: #fff;
  padding: 3.333333vw 0;
  border: none;
  font-weight: 500;
}
</style>